@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "helpers";

* {
    box-sizing: border-box;
}

*:focus {
    outline: none;
}

a, button, input[type=submit] {
    transition: .2s;
}

body {
    font-family: $font-Montserrat;
    padding-top: 10px;
    color: #2c1f40;
    max-width: 1920px;
    margin: 0 auto;
    width: 100%;
}

.wrapper {
    width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
}

.row {
    display: flex;
}

header {
    margin-bottom: 35px;
}

.header__row {
