.main-menu {
    flex: 1;
}

.main-menu nav {
    display: flex;
    justify-content: space-between;
}

.cart {
    width: 165px;
    text-align: center;
}

.main-menu a.catalog {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    span {
        display: block;
        height: 2px;
        width: 10px;
        background-color: #222;
    }
    .icon {
        width: 20px;
        height: 10px;
        margin-right: 10px;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        &:after, &:before {
            content: '';
            width: 20px;
            height: 2px;
            background-color: #222;
            position: absolute;
            top: 0;
            left: 0;
        }
        &:before {
            top: auto;
            bottom: 0;
        }
    }
}

.main-menu a {
    font-size: 14px;
    font-weight: 600;
    text-transform: uppercase;
    text-decoration: none;
    color: #222;
    line-height: 48px;
    &.active, &:hover {
        color: #b27aff;
        span {
            background-color: #b27aff;
        }
        .icon:after, .icon:before {
            background-color: #b27aff;
        }
    }
}

.cart__row {
    margin-left: 30px;
    justify-content: center;
    align-items: center;
    font-size: 13px;
    font-weight: 500;
    color: #190438;
    text-decoration: none;
    .image {
        margin-right: 15px;
        position: relative;
        &:after {
            content: '';
            width: 15px;
            height: 15px;
            border-radius: 50%;
            background-color: #b27aff;
