}

.lSSlideOuter .lSPager.lSpg>li a {
    width: 30px;
    height: 2px;
    border-radius: 0;
    background-color: #9f9ca5;
}
.lSSlideOuter .lSPager.lSpg>li:hover a {
    background-color: #b27aff;
}
.lSSlideOuter .lSPager.lSpg>li.active a {
    background-color: #b27aff;
    width: 60px;
}
.lSAction>a {
    background: #f1f0f2;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    opacity: 1;
}
.lSAction>.lSPrev {
    left: 0;
}
.lSAction>.lSPrev span {
    display: block;
    position: relative;
    width: 7px;
    height: 15px;
    margin: 13px 16px;
    &:before, &:after {
        position: absolute;
        border-radius: 2px;
        top: 6px;
        width: 8px;
        height: 2px;
        content: '';
        background: rgb(34, 34, 34);
    }
    &:before {
        left: 0;
        transform: rotate(-45deg);
        transform-origin: 0 100%;
    }
    &:after {
        left: 0;
        transform: rotate(45deg);
        transform-origin: 0 0;
    }
}
.lSAction>.lSNext {
    right: 0;
}
.lSAction>.lSNext span {
    display: block;
    position: relative;
    width: 7px;
    height: 15px;
    margin: 13px 16px;
    &:before, &:after {
        position: absolute;
        border-radius: 2px;
        top: 6px;
        width: 8px;
        height: 2px;
        content: '';
        background: rgb(34, 34, 34);
    }
    &:before {
        right: 0;
        transform: rotate(-45deg);
        transform-origin: 100% 100%;
        top: 4px;
    }
    &:after {
        right: 0;
        transform: rotate(45deg);
        transform-origin: 100% 0;
    }
}

